<template>
  <div class="course-practise-score">
    <head-top title="得分统计">
    </head-top>

    <van-cell-group v-if="scoreList && scoreList.length>0">
      <van-cell class="cell_main" v-for="(item, index) in scoreList" :key="index">
        <template slot="icon">
          <img :src="item.imgSrc" alt=""/>
        </template>
        <template slot="title">
          <div class="flex-between">
          <span class="line_title">{{item.studentName}}</span>
            <span class="line_right">名次：{{item.rank}}</span>
          </div>
        </template>
        <template slot="label">
         <div class="line_title flex-between">
           <span>单选：{{item.singleScore}}</span>
           <span>多选：{{item.multiScore}}</span>
           <span>判断：{{item.judgeScore}}</span>
           <span>问答：{{item.questionScore}}</span>
           <span>总分：{{item.totalScore}}</span>
         </div>
        </template>
<!--        <template slot="right-icon" >-->
<!--          <span class="line_right"> {{item.rank}}</span>-->
<!--        </template>-->
      </van-cell>
    </van-cell-group>
    <r-lack-data v-else></r-lack-data>
  </div>
</template>

<script>

import defaultImg from "../../../../assets/boy_img.png";
export default {
  name: "practiseScore",
  data () {
    return {
      libraryInfo: this.$store.getters.practiseLibrary,
      scoreList: []
    };
  },
  created () {
    this.getScorePerson();
  },
  methods: {
    getScorePerson () {
      this.scoreList = [];
      this.$gatewayHttp.classPractise.getScorePerson({
        cipID: this.libraryInfo.taskID
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              return a["Ranking"] > b["Ranking"] ? 1 : -1;
            });
            res.Datas.forEach(item => {
              this.scoreList.push({
                imgSrc: defaultImg,
                studentName: item["StudentName"],
                rank: item["Ranking"],
                totalScore: item["TotalScore"],
                singleScore: item["SingleScore"],
                multiScore: item["MultipleScore"],
                judgeScore: item["JudgeScore"],
                questionScore: item["QandAScore"],
                studentID: item["StudentID"]
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-practise-score {
  min-height: 100%;
  background-color: #fff;
}
</style>
